@import url('https://fonts.googleapis.com/css?family=Original+Surfer&display=swap');

$headings-font-family: 'Original Surfer', serif;

.btn {
    font-family: $headings-font-family;
}


h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
    font-family: $headings-font-family;
}


.blockquote-footer {
    font-family: $headings-font-family;
}


label {
    font-family: $headings-font-family;
}

.nav-pills {
    .nav-link {
        font-family: $headings-font-family;
    }
}


.navbar-light {
    .navbar-nav {
        .nav-link {
            font-family: $headings-font-family;
        }
    }
}


.lb-dataContainer {
    .lb-caption {
        font-family: $headings-font-family;
    }
}


.datepicker thead td, .datepicker thead th {
    font-family: $headings-font-family;
}


.bg-hero-text {

    span.bg-hero-text-content {
        font-family: $headings-font-family;
    }
}


.bg-heading-text, .position-relative {
    position: relative;

    span.bg-heading-text-content {
        font-family: $headings-font-family;
    }

}


.accordion-toggle {
    font-family: $headings-font-family;
}
